import Vue from 'vue';
import Vuex from 'vuex';
import App from './App.vue';
import router from './router';
import vuetify from './plugins/vuetify';
import './assets/icon/iconfont';
// import './assets/icon/iconfont.css';

Vue.use(Vuex);

Vue.config.productionTip = false;

const store = new Vuex.Store({
  state: {
    selectBookId: 0,
    selectBookName: '',
    unKeepAliveComponent: '',
    fontSize: localStorage.fontSize || 1,
    isDarkMode: localStorage.isDarkMode === 'true',
    isDrawerOpen: false,
  },
  mutations: {
    updateSelectBookId(state, selectBookId) {
      state.selectBookId = selectBookId;
    },
    updateSelectBookName(state, selectBookName) {
      state.selectBookName = selectBookName;
    },
    updateUnKeepAliveComponent(state, unKeepAliveComponent) {
      state.unKeepAliveComponent = unKeepAliveComponent;
    },
    updateFontSize(state, fontSize) {
      state.fontSize = fontSize;
      localStorage.fontSize = fontSize;
    },
    updateDarkMode(state, darkMode) {
      state.isDarkMode = darkMode;
      localStorage.isDarkMode = darkMode;
      // if (darkMode) {
      //   document.body.style.backgroundColor = '#1d2935';
      // } else {
      //   document.body.style.backgroundColor = '';
      // }
    },
    updateDrawerState(state, isDrawerOpen) {
      state.isDrawerOpen = isDrawerOpen;
    },
  },
});

new Vue({
  router,
  store,
  vuetify,
  render: (h) => h(App),
}).$mount('#app');
