<script>
export default {
  name: 'Setting',
  data() {
    return {
      isDarkMode: this.$store.state.isDarkMode,
    };
  },
  methods: {
    updateDarkMode() {
      this.$store.commit('updateDarkMode', this.isDarkMode);
      localStorage.isDarkMode = this.isDarkMode;
      this.$vuetify.theme.dark = this.isDarkMode;
    },
  },
};
