<template>
  <v-container fluid>
    <div v-for="(question, index) in questions"
         :key="index"
         v-show="index >= (currentPage - 1) * itemNumberPerPage
                && index < currentPage * itemNumberPerPage"
         class="input-question"
         v-bind:style="{ fontSize: fontSize + 'rem',
                         marginBottom: fontSize*2 + 'rem' }">
      <div class="question-title"
           v-bind:class="{ wrongCount: check(answers[index], question.answer),
                                wrong: check(answers[index], question.answer)
                                      && !isBeforeSubmit }">
        <span class="index-style">{{ (index + 1) + '. ' }}</span>
        <span>{{ question.question }}</span>
      </div>
      <v-text-field
        filled
        v-model="answers[index]" placeholder="请输入答案"
        type="tel"
        :readonly="!isBeforeSubmit"
      ></v-text-field>
      <div v-if="check(answers[index], question.answer) && !isBeforeSubmit">
        {{ '正确答案: ' + question.answer }}
      </div>
    </div>
    <v-pagination
      v-model="currentPage"
      :length="pageNumber"
      @previous="backTop"
      @next="backTop"
      prev-icon="$chevronLeft"
      next-icon="$chevronRight"
    ></v-pagination>
    <v-dialog v-model="isDialogShow" persistent max-width="290">
      <v-card>
        <v-card-title class="dialog-headline">
          <svg class="icon-size icon" aria-hidden="true">
            <use :xlink:href="icon"></use>
          </svg>
          <span class="title">{{ title }}</span>
        </v-card-title>
        <v-card-text v-html="result"></v-card-text>
        <v-card-actions>
          <v-btn class="dialog-button" color="primary" text @click="isDialogShow = false">确认</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- <van-button type="info" @click="submit"
                v-if="isBeforeSubmit">提交</van-button> -->
    <v-btn
      class="control-button"
      color="primary"
      @click.stop="submit"
      v-if="isBeforeSubmit"
    >
      提交
    </v-btn>
    <v-btn
      class="control-button"
      color="primary"
      @click="retry"
      v-if="!isBeforeSubmit"
    >
      重新练习
    </v-btn>
    <v-btn
      class="control-button"
      color="primary"
      @click="showResult"
      v-if="!isBeforeSubmit"
    >
      测试结果
    </v-btn>
  </v-container>
