<script>
// @ is an alias to /src
// import Vue from 'vue';
// import {
//   Button, CellGroup, Cell, Field, Dialog, Pagination,
// } from 'vant';

// Vue.use(Button);
// Vue.use(CellGroup);
// Vue.use(Cell);
// Vue.use(Field);
// Vue.use(Dialog);
// Vue.use(Pagination);

export default {
  name: 'InputQuestion',
  props: {
    questions: Array,
  },
  data() {
    return {
      answers: Array(this.questions.length).fill(''),
      isBeforeSubmit: true,
      icon: '',
      title: '',
      result: '',
      currentPage: 1,
      itemNumberPerPage: 5,
      isDialogShow: false,
    };
  },
  methods: {
    check(userInput, answer) {
      if (userInput !== answer) {
        return true;
      }
      return false;
    },
    getResult() {
      const wrongAnswers = document.getElementsByClassName('wrongCount');
      const wrongAnswerNumber = wrongAnswers.length;
      const totalAnswerNumber = this.questions.length;
      const accuracy = 1 - wrongAnswerNumber / totalAnswerNumber;
      const resultContent = `正确: ${totalAnswerNumber - wrongAnswerNumber}, 错误: ${wrongAnswerNumber}。`;
      if (accuracy < 0.6) {
        this.icon = '#icon--flushed';
        this.title = '继续加油';
      } else if (accuracy > 0.8) {
        this.icon = '#icon--cool';
        this.title = '你真棒';
      } else {
        this.icon = '#icon--happy-1';
        this.title = '不错哦';
      }

      let allWrongQuestionIndex = '';
      if (wrongAnswerNumber !== 0) {
        let wrongQuestionIndex = '';
        wrongAnswers.forEach((element) => {
          const questionTitle = element.firstChild.innerHTML;
          const questionIndex = questionTitle.substring(0, questionTitle.indexOf('.'));
          wrongQuestionIndex += `${questionIndex}, `;
        });
        allWrongQuestionIndex = `错误题目：${wrongQuestionIndex.slice(0, -2)}。`;
      }

      this.result = `${resultContent}<br>${allWrongQuestionIndex}`;
    },
    showResult() {
      this.isDialogShow = true;
    },
    submit() {
      this.isBeforeSubmit = false;
      this.getResult();
      this.showResult();
    },
    retry() {
      this.isBeforeSubmit = true;
      this.currentPage = 1;
      this.answers = Array(this.questions.length).fill('');
      this.backTop();
    },
    backTop() {
      window.scroll(0, 0);
    },
  },
  computed: {
    fontSize() {
      const currentFontSizeValue = this.$store.state.fontSize - 1;
      const fontSize = `1.${currentFontSizeValue}`;
      return fontSize;
    },
    pageNumber() {
      const totalQuestionNumber = this.questions.length;
      const totalPageNumber = Math.ceil(totalQuestionNumber / this.itemNumberPerPage);
      return totalPageNumber;
    },
  },
};
</script>
