<template>
  <v-app-bar app dense v-show="title">
    <!-- <v-app-bar-nav-icon v-show="bookId"
                        @click="togglePopup">
    </v-app-bar-nav-icon> -->
    <v-app-bar-nav-icon @click="toggleDrawer">
      <v-icon v-if="$route.path === '/'">$library</v-icon>
      <v-icon v-if="$route.path === '/setting'">$setting</v-icon>
      <v-icon v-if="$route.path === '/practice'">$menu</v-icon>
    </v-app-bar-nav-icon>
    <v-toolbar-title>
      {{ title }}
    </v-toolbar-title>
    <v-spacer></v-spacer>
    <v-menu
      offset-y
      :close-on-content-click="false"
      v-if="this.$route.path === '/practice'"
    >
      <template v-slot:activator="{ on, attrs }">
        <v-btn
          icon
          v-bind="attrs"
          v-on="on"
        >
          <v-icon>$elipsis</v-icon>
        </v-btn>
      </template>

    <v-card>
      <v-list>
        <v-list-item>
          <v-list-item-title>页面设置</v-list-item-title>
        </v-list-item>
      </v-list>

      <v-divider></v-divider>

      <v-list>
        <v-list-item>
          <v-list-item-content>夜间模式</v-list-item-content>
          <v-list-item-action>
            <v-switch v-model="isDarkMode" inset @change="updateDarkMode">
            </v-switch>
          </v-list-item-action>
        </v-list-item>

        <v-list-item>
          <v-list-item-content>文字大小</v-list-item-content>
          <v-list-item-icon>
              <v-icon @click="decreaseFontSize" :disabled="currentFontSize === 1">$minus</v-icon>
              <span class="mx-2">{{ currentFontSize }}</span>
              <v-icon @click="increaseFontSize" :disabled="currentFontSize === 9">$plus</v-icon>
              <!-- <v-btn class="mx-2" fab small color="primary"
                      @click="decreaseFontSize"
                      :disabled="currentFontSize === 1">
                <v-icon dark>mdi-minus</v-icon>
              </v-btn>
              {{ currentFontSize }}
              <v-btn class="mx-2" fab small color="primary"
                      @click="increaseFontSize"
                      :disabled="currentFontSize === 10">
                <v-icon dark>mdi-plus</v-icon>
              </v-btn> -->
          </v-list-item-icon>
        </v-list-item>
      </v-list>
    </v-card>
  </v-menu>
  </v-app-bar>
</template>

<script>
export default {
  name: 'Header',
  data() {
    return {
      currentFontSize: +this.$store.state.fontSize,
      isDarkMode: this.$store.state.isDarkMode,
    };
  },
  methods: {
    toggleDrawer() {
      this.$store.commit('updateDrawerState', !this.$store.state.isDrawerOpen);
    },
    increaseFontSize() {
      this.currentFontSize += 1;
      this.$store.commit('updateFontSize', this.currentFontSize);
    },
    decreaseFontSize() {
      this.currentFontSize -= 1;
      this.$store.commit('updateFontSize', this.currentFontSize);
    },
    updateDarkMode() {
      this.$store.commit('updateDarkMode', this.isDarkMode);
      this.$vuetify.theme.dark = this.isDarkMode;
    },
  },
  computed: {
    title() {
      const page = this.$route.path;
      if (page === '/') {
        return '酷特书库';
      }
      if (page === '/practice') {
        return this.$store.state.selectBookName;
      }
      return '设置';
    },
  },
};
</script>
