         class="mt-8 mb-4 font-weight-black"
         v-bind:style="{ fontSize: fontSize + 'rem',
                       lineHeight: (fontSize - 1) / 2 + 1 }">题目已经抵达战场</div>
    <v-btn v-if="bookId && !isLoadingTitle && !isStartPractice"
           color="primary"
           @click="toggleDrawer">
      开始战斗
    </v-btn>
    <!-- <van-button v-if="bookId && !isLoadingTitle && !isStartPractice"
                type="info" @click="togglePopup">
      开始战斗
    </van-button> -->
    <!-- load question -->
    <v-progress-circular
      v-if="isLoadingQuestion"
      indeterminate
      color="primary"
    ></v-progress-circular>
    <!-- <van-loading v-if="isLoadingQuestion" color="#1989fa" size="50px" /> -->
    <!-- <van-cell class="bold" v-if="!isLoadingQuestion" :value='currentQuestionTitle' /> -->
