<script>
// import Vue from 'vue';
import axios from 'axios';
// import {
//   Popup, Button, CellGroup, Cell, NavBar, Loading,
//   Toast, DropdownMenu, DropdownItem, Stepper, Switch,
// } from 'vant';
import SelectQuestion from '@/components/SelectQuestion.vue';
import InputQuestion from '@/components/InputQuestion.vue';

// Vue.use(Popup);
// Vue.use(Button);
// Vue.use(CellGroup);
// Vue.use(Cell);
// Vue.use(NavBar);
// Vue.use(Loading);
// Vue.use(Toast);
// Vue.use(DropdownMenu);
// Vue.use(DropdownItem);
// Vue.use(Stepper);
// Vue.use(Switch);

export default {
  name: 'Practice',
  components: {
    SelectQuestion,
    InputQuestion,
  },
  data() {
    return {
      currentQuestionTitle: '',
      currentFontSize: +this.$store.state.fontSize,
      rawTitles: [],
      rawQuestions: [],
      bookId: this.$store.state.selectBookId,
      bookName: this.$store.state.selectBookName,
      isPopupShow: false,
      isSettingShow: false,
      isLoadingTitle: true,
      isStartPractice: false,
      isLoadingQuestion: false,
      isDarkMode: this.$store.state.isDarkMode,
      snackbar: false,
    };
  },
  mounted() {
    // if (this.bookId !== 0) {
    //   axios
    //     // .get(`https://ww4dlncx61.execute-api.ap-northeast-1.amazonaws.com/prod/loadMenu?book_id=${this.bookId}`)
    //     .get('https://ww4dlncx61.execute-api.ap-northeast-1.amazonaws.com/prod/loadMenu?book_id=1')
    //     .then((response) => {
    //       this.rawTitles = response.data;
    //       this.isLoadingTitle = false;
    //       this.enableKeepAlive();
    //     });
    // }
    axios
      // .get(`https://ww4dlncx61.execute-api.ap-northeast-1.amazonaws.com/prod/loadMenu?book_id=${this.bookId}`)
      .get('https://ww4dlncx61.execute-api.ap-northeast-1.amazonaws.com/prod/loadMenu?book_id=1')
      .then((response) => {
        this.rawTitles = response.data;
        this.isLoadingTitle = false;
        this.enableKeepAlive();
      });
  },
  methods: {
    togglePopup() {
      // this.isPopupShow = !this.isPopupShow;
      this.drawer = !this.drawer;
    },
    toggleDrawer() {
      this.$store.commit('updateDrawerState', !this.$store.state.isDrawerOpen);
    },
    toggleSetting() {
      this.isSettingShow = !this.isSettingShow;
    },
    showQuestions(titleId, title, available) {
      if (available) {
        this.isStartPractice = true;
        // this.togglePopup();
        this.toggleDrawer();
        if (this.currentQuestionTitle !== title) {
          this.currentQuestionTitle = title;
          this.isLoadingQuestion = true;
          axios
            .get(`https://ww4dlncx61.execute-api.ap-northeast-1.amazonaws.com/prod/loadMenu?title_id=${titleId}`)
            .then((response) => {
              this.rawQuestions = response.data;
              this.isLoadingQuestion = false;
            });
        }
      } else {
        // Toast('非常抱歉，这道题还没准备好，请选择标题为黑色的题目开始练习吧');
        this.snackbar = true;
      }
    },
    addSpace(noSpaceString) {
      const separators = ['+', '-', '(', ')', '*', '/', ':', '?', '='];
      let spaceString = noSpaceString;
      separators.forEach((element) => {
        spaceString = spaceString.replace(element, ` ${element} `);
      });
      return spaceString;
    },
    enableKeepAlive() {
      this.$store.commit('updateUnKeepAliveComponent', '');
    },
    updateFontSize() {
      this.$store.commit('updateFontSize', this.currentFontSize);
      localStorage.fontSize = this.currentFontSize;
    },
    increaseFontSize() {
      this.currentFontSize += 1;
      this.$store.commit('updateFontSize', this.currentFontSize);
      localStorage.fontSize = this.currentFontSize;
    },
    decreaseFontSize() {
      this.currentFontSize -= 1;
      this.$store.commit('updateFontSize', this.currentFontSize);
      localStorage.fontSize = this.currentFontSize;
    },
    updateDarkMode() {
      this.$store.commit('updateDarkMode', this.isDarkMode);
      localStorage.isDarkMode = this.isDarkMode;
    },
  },
  computed: {
    titles() {
      const classifiedTitles = {};
      let mainTitle = '';
      this.rawTitles.forEach((title) => {
        if (title.is_sub_title === 0) {
          mainTitle = title.title;
        } else if (title.is_sub_title === 1) {
          // avoid classifiedTitles[mainTitle] undefined
          classifiedTitles[mainTitle] = classifiedTitles[mainTitle] || [];
          classifiedTitles[mainTitle].push(title);
        }
      });
      return classifiedTitles;
    },
    selectTitles() {
      return Array(this.titles.length).fill('');
    },
    questions() {
      const unformatQuestions = this.rawQuestions;
      if (unformatQuestions.length > 0 && unformatQuestions[0].question.includes('@@')) {
        const formatQuestions = [];
        const alphabet = 'abcdefghijk'.split('');
        for (let i = 0; i < unformatQuestions.length; i += 1) {
          const splitIndex = unformatQuestions[i].question.indexOf('@@');
          const noSpaceQuestion = unformatQuestions[i].question.substring(0, splitIndex);
          const questionValue = this.addSpace(noSpaceQuestion);
          const selectionArray = unformatQuestions[i].question.substring(splitIndex + 2).split('@');
          let selectionValue = {};
          const answerValue = unformatQuestions[i].answer;
          let questionObj = {};
          for (let j = 0; j < selectionArray.length; j += 1) {
            selectionValue = Object.assign(selectionValue, { [alphabet[j]]: selectionArray[j] });
          }
          questionObj = {
            question: questionValue,
            selections: selectionValue,
            answer: answerValue,
          };
          formatQuestions.push(questionObj);
        }
        return formatQuestions;
      }
      for (let i = 0; i < unformatQuestions.length; i += 1) {
        unformatQuestions[i].question = this.addSpace(unformatQuestions[i].question);
      }
      return unformatQuestions;
    },
    fontSize() {
      const currentFontSizeValue = this.$store.state.fontSize - 1;
      const fontSize = `1.${currentFontSizeValue}`;
      return fontSize;
    },
  },
};
</script>

